body,html {
  background-color: rgb(15, 13, 54);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:white;
  font-size: 12px;
}
.App{
  margin-left: 5%;
  margin-top: 30px;
  background-color: rgb(90, 0, 0);
  width:38vw;
  padding:5px;
  padding-left: 30px;
  text-align: left;
  float: left;
}
#cont2{
  margin-top: 30px;
  background-color: rgb(35, 116, 116);
  float:right;
  width:38vw;
  margin-right: 14%;
  padding:10px;
  padding-bottom:85px;
  text-align: center !important;
}
#cont2>p{
  display: inline-block;
  place-self: center;
  height:30px;
  width:200px;
  background-color: aliceblue;
  color:black;
  align-self: center;
  padding: 2px;
  padding-top: 5px;
  font-size: 15px;
}
img{
  height:150px;
  width:150px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.input-validation-error
{
   color:red;
}
input,select,option {
  width: 90%;
  padding: 5px 10px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
button{
  font-size: 15px;
  background-color: greenyellow;
  height:40px;
  width:200px;
  border-radius: 10px;
}
options:hover{
  background-color: red;
  border-radius: '2em';
  color: '#172B4D';
  display: 'inline-block';
  font-size: 12;
  font-weight: 'normal';
  line-height: '1';
  padding: '0.16666666666667em 0.5em';
  text-align: 'center'

}
#msg{
  font-size:15px;
  color:black;
  height:30px;
  width: 90%;
  background-color: aliceblue;
  border-color: blue;
  padding: 2px 10px;
  visibility: hidden;

}
#errmsg{
  font-size:15px;
  color:black;
  height:30px;
  width: 90%;
  background-color: rgba(255, 0, 0, 0.507);
  border:2px solid red;
  padding: 2px 10px;
}
#scsmsg{
  font-size:15px;
  color:black;
  height:30px;
  width: 90%;
  background-color :rgba(0, 128, 0, 0.5);
  border:2px solid green;
  padding: 2px 10px;
}
#cross{
  float: right;
}
#cross:hover{
  cursor: pointer;
}
@media screen and (max-width: 1100px){
  #cont2{
    margin-right: 10%;
  }
}
@media screen and (max-width: 611px){
  #cont2{
    margin-right: 6%;
  }
}